* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  min-height: 100vh;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.header {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.header h1 {
  color: #fff;
  font-size: 1.5rem;
}

.header-actions {
  display: flex;
  gap: 0.5rem;
}

.add-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 0.6rem 1.2rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: transform 0.2s, box-shadow 0.2s;
}

.add-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.reset-btn {
  background: rgba(255, 255, 255, 0.1);
  color: #aaa;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 0.6rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s;
}

.reset-btn:hover {
  background: rgba(255, 255, 255, 0.15);
  color: #fff;
}

.add-form {
  background: rgba(255, 255, 255, 0.05);
  padding: 1rem 2rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.add-form input {
  flex: 1;
  min-width: 200px;
  padding: 0.6rem 1rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
  font-size: 0.9rem;
}

.add-form input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.form-actions {
  display: flex;
  gap: 0.5rem;
}

.add-form button {
  padding: 0.6rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  border: none;
  transition: all 0.2s;
}

.add-form button:first-child {
  background: #667eea;
  color: white;
}

.add-form button:last-child {
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.board {
  flex: 1;
  display: flex;
  gap: 1.5rem;
  padding: 2rem;
  overflow-x: auto;
}

.column {
  flex: 1;
  min-width: 300px;
  max-width: 400px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 12px;
  padding: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.column-title {
  color: #fff;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.task-count {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.6);
  padding: 0.2rem 0.6rem;
  border-radius: 12px;
  font-size: 0.75rem;
}

.tasks {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  min-height: 200px;
}

.task {
  background: rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  padding: 1rem;
  cursor: grab;
  transition: all 0.2s;
  border-left: 4px solid transparent;
}

.task:hover {
  background: rgba(255, 255, 255, 0.12);
  transform: translateX(4px);
}

.task:active {
  cursor: grabbing;
}

/* Status-based task styling */
.task.status-pending {
  border-left-color: #f59e0b;
}

.task.status-active {
  border-left-color: #3b82f6;
}

.task.status-completed {
  border-left-color: #10b981;
}

.task.status-blocked {
  border-left-color: #ef4444;
  background: rgba(239, 68, 68, 0.1);
}

.task-header {
  display: flex;
  align-items: flex-start;
  gap: 0.5rem;
}

.status-badge {
  font-size: 0.9rem;
  flex-shrink: 0;
}

.task-header h3 {
  color: #fff;
  font-size: 0.95rem;
  font-weight: 500;
  flex: 1;
  word-break: break-word;
}

.delete-btn {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.3);
  font-size: 1.2rem;
  cursor: pointer;
  padding: 0;
  line-height: 1;
  transition: color 0.2s;
}

.delete-btn:hover {
  color: #ef4444;
}

.task-desc {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
  margin-top: 0.5rem;
  line-height: 1.4;
}

.blocked-reason {
  background: rgba(239, 68, 68, 0.2);
  color: #fca5a5;
  padding: 0.5rem;
  border-radius: 6px;
  font-size: 0.8rem;
  margin-top: 0.5rem;
}

.task-meta {
  display: flex;
  gap: 0.75rem;
  margin-top: 0.75rem;
  flex-wrap: wrap;
}

.assignee {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.75rem;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.timestamp {
  color: rgba(255, 255, 255, 0.3);
  font-size: 0.7rem;
}

.task-actions {
  margin-top: 0.75rem;
  padding-top: 0.75rem;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.block-btn {
  background: rgba(239, 68, 68, 0.2);
  color: #fca5a5;
  border: none;
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.75rem;
  transition: all 0.2s;
}

.block-btn:hover {
  background: rgba(239, 68, 68, 0.3);
}

.block-btn.unblock {
  background: rgba(16, 185, 129, 0.2);
  color: #6ee7b7;
}

.block-btn.unblock:hover {
  background: rgba(16, 185, 129, 0.3);
}

.block-form {
  margin-top: 0.75rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.block-form input {
  padding: 0.5rem;
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 6px;
  background: rgba(239, 68, 68, 0.1);
  color: #fff;
  font-size: 0.85rem;
}

.block-form input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.block-form-actions {
  display: flex;
  gap: 0.5rem;
}

.block-form button {
  flex: 1;
  padding: 0.4rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.2s;
}

.block-form button:first-child {
  background: #ef4444;
  color: white;
}

.block-form button:last-child {
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.footer {
  background: rgba(255, 255, 255, 0.02);
  padding: 1rem 2rem;
  text-align: center;
  color: rgba(255, 255, 255, 0.3);
  font-size: 0.8rem;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

/* Drag states */
.column:has(.task:active) {
  background: rgba(102, 126, 234, 0.1);
}

/* Loading and status */
.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.loader {
  color: #fff;
  font-size: 1.5rem;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.saving-indicator {
  color: #fbbf24;
  font-size: 0.85rem;
  animation: pulse 1s infinite;
}

.error-indicator {
  color: #f87171;
  font-size: 0.85rem;
  cursor: help;
}

.error-banner {
  background: rgba(239, 68, 68, 0.2);
  color: #fca5a5;
  padding: 0.75rem 2rem;
  text-align: center;
  font-size: 0.85rem;
  border-bottom: 1px solid rgba(239, 68, 68, 0.3);
}

/* Responsive */
@media (max-width: 768px) {
  .header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .board {
    padding: 1rem;
  }
  
  .column {
    min-width: 280px;
  }
  
  .add-form {
    flex-direction: column;
  }
  
  .add-form input {
    min-width: 100%;
  }
}
